import React, { Component } from 'react';
import {
    Navbar, NavbarBrand, Nav, NavbarToggler, Collapse, NavItem,
    Button, Modal, ModalHeader, ModalBody,
    Form, FormGroup, Input, Label, Col
} from 'reactstrap';
import { NavLink } from 'react-router-dom';


class Header extends Component {

    constructor(props) {
        super(props);

        this.state = {
            isNavOpen: false,
            isModalOpen: false
        }

        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);

    }


    toggleModal() {
        this.setState({ isModalOpen: !this.state.isModalOpen })
    }


    handleLogin(event) {
        this.toggleModal();
        alert("Username: " + this.username.value + " Password: " + this.password.value
            + " Remember: " + this.remember.checked);
        event.preventDefault();

    }

    render() {

        const toggleNav = () => {
            this.setState({ isNavOpen: !this.state.isNavOpen })
        }


        return (



            <div>
                <React.Fragment>
                    <Navbar dark expand="md">
                        <div className="container">
                            <NavbarToggler onClick={toggleNav} />
                            <NavbarBrand className="mr-auto">
                                <NavLink to='/home'>
                                    <img src='assets/images/logo.png' height="30" width="41" alt='Ristorante Con Fusion' />
                                </NavLink>
                            </NavbarBrand>
                            <Collapse isOpen={this.state.isNavOpen} navbar>
                                <Nav navbar>
                                    <NavItem>
                                        <NavLink className="nav-link" to='/home'><span className="fa fa-home fa-lg"></span> Home</NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to='/aboutus'><span className="fa fa-info fa-lg"></span> About Us</NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to='/menu'><span className="fa fa-list fa-lg"></span> Menu</NavLink>
                                    </NavItem>
                                    <NavItem>
                                        <NavLink className="nav-link" to='/contactus'><span className="fa fa-address-card fa-lg"></span> Contact Us</NavLink>
                                    </NavItem>
                                </Nav>
                                <Nav navbar className="ml-auto">
                                    <NavItem>
                                        <Button outline onClick={this.toggleModal}><span className="fa fa-sign-in fa-lg"></span> Login</Button>
                                    </NavItem>
                                </Nav>
                            </Collapse>
                        </div>
                    </Navbar>
                    <div className="jumbotron">
                        <div className="container">
                            <div className="row row-header">
                                <div className="col-12 col-sm-6">
                                    <h1>Ristorante con Fusion</h1>
                                    <p>We take inspiration from the World's best cuisines, and create a unique fusion experience. Our lipsmacking creations will tickle your culinary senses!</p>
                                </div>
                            </div>
                        </div>
                    </div>

                    <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
                        <ModalHeader toggle={this.toggleModal}>
                            <h1>Login</h1>
                        </ModalHeader>
                        <ModalBody>
                            <Form onSubmit={this.handleLogin}>
                                <FormGroup row>
                                    <Label htmlFor="username" md={2}>Username</Label>
                                    <Col md={10}>
                                        <Input id="username" name="username" type="text" innerRef={(input) => this.username = input} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Label htmlFor="password" md={2}>Password</Label>
                                    <Col md={10}>
                                        <Input id="password" name="password" type="password" innerRef={(input) => this.password = input} />
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Col md={{ size: 10, offset: 2 }}>
                                        <FormGroup check>
                                            <Label check>
                                                <Input type="checkbox"
                                                    name="remember" id="remember" innerRef={(input) => this.remember = input} /> {' '}
                                                <strong>Remember me</strong>
                                            </Label>
                                        </FormGroup>
                                    </Col>
                                </FormGroup>
                                <FormGroup row>
                                    <Col md={{ size: 10, offset: 2 }}>
                                        <Button type="submit" value="submit" className="btn btn-primary">Login</Button>
                                    </Col>
                                </FormGroup>


                            </Form>

                        </ModalBody>

                    </Modal>

                </React.Fragment>

            </div >
        );
    }
}

export default Header;